
:root {
  --main-yellow: rgba(255, 255, 120, 1);
  --main-gray: rgba(128, 128, 128, 1);
}

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}


.Kapitola-gray {
  font-weight: 900;
  font-size: 35px;
  color:white;
  background-color:var(--main-gray);
  margin: 0px;
  padding: 15px;
  /* height: 55px; */
  display: flex;
  justify-content: center;
  align-items: center;
} 

.Kapitola-yellow {
  font-weight: 900;
  font-size: 35px;
  color:black;
  background-color: var(--main-yellow);
  margin: 0px;
  padding: 15px;
  /* height: 55px; */
  /* display: flex; */
  justify-content: center;
  align-items: center;
} 

/* O nás */

.Images {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.Logos {
  margin: 10px;
  max-width: 200px;
  max-height: 100px;
}

#Oregon-logo {
  max-width: 450px;
  max-height: 250px;
  width: 50%;
}

.Text {
  font-weight: 400;
  color: white;
  background-color:var(--main-gray);
  margin: 0;
  text-align: justify;
}

/* Skupiny karet */

.CardGroup {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.card-footer.yellow {
  background-color:var(--main-yellow);
}

.card-footer.gray {
  background-color:var(--main-gray);
}



.BombCardGroup{
  width: 30%;
  min-width: 400px;
  margin: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  box-shadow: 5px 5px 20px rgba(100, 100, 100, 0.7);
  align-self: stretch;
  border: none;
}

/* navbar start */
/* 
#basic-navbar-nav, #navbar-collapse, #collapse  
{
  position: absolute;
  right: 1em;
} */

.navbar-dark .navbar-brand {
  font-weight: 900;
  font-size: 20px;
  color: white;
}

.navbar-dark .navbar-brand:hover {
  color: red;
}

.navbar-dark .navbar-nav .nav-link {
  font-weight: 400;
  font-size: 20px;
  color: white;
  padding: 0px 30px;
}

.navbar-dark .navbar-nav .nav-link:hover {
  color: red;
}

.bg-dark {
  background-color: var(--main-gray) !important;
}

.cenik-img {
  width: 100%;
  object-fit: cover;
}

/* navbar end */

h1 {
  font-weight: 900;
  font-size: 35px;
}

h3 {
  font-weight: 900;
  font-size: 25px;
}

p {
  font-weight: 400;
  font-size: 20px;
  padding: 0 10% ;
}

/* li ul {
  font-weight: 400;
  font-size: 20px;
  padding: 0 10%;
} */

.main-bg{
  width: 100%;
  background-image: url("../src/images/Sharpening_cut_1920.png");
  background-repeat: no-repeat;
  background-size: cover;
}

#bg-info {
  padding-top: 20vh;
  padding-bottom: 3vh;
  /* margin-left: 3%; */
  text-align: left;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 800px;
}

#logo-name {
  font-family: "Stencil Std";
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  text-align: center;
}

#main-logo {
  min-width: 300px;
  max-width: 800px;
  width: 90%;
  margin: 3%;
}

#main-list {
  font-weight: 900;
  font-size: 25px;
  color: black;
}


/* Bomičk */

