.CardInfo {
  display: flex;
}

.BombText {
  color: white;
  text-align: left;
  margin: 0;
}

.modal-body {
  text-align: left;
  font-size: 20px;
  padding: 20px 60px; 
}

.modal-body p {
  padding: 0; 
}

.BombCard {
  width: 40%;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  box-shadow: 5px 5px 20px rgba(100, 100, 100, 0.7);
}


.BombCardImg {
  max-height: 270px;
  /* min-width: 100px; */
  margin: auto;
  background-color: red;
  position: relative;
  object-fit: cover;
}

