.gray-background {
  background-color: var(--main-gray);
  color: white;
}

.yellow-background {
  background-color: var(--main-yellow);
  color: black;
}

.white-color {
  color: white;
}

.black-color {
  color: black;
}

:target {
  display: block;
  position: relative;
  top: -56px; 
  visibility: hidden;
}

.Kontakty {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  text-align: left;
}

.Kontakty p {
  margin: 0px 0px;
  padding: 0px;
}

.Kontakty div {
  padding: 10px 30px;
}

#box1 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

#box1 a {
  color: white;
  font-size: 20px;
}

#box2 {
  text-align: center;
}

#contact-title {
  margin: 0;
  padding: 0;
}


#mapa {
  background-color:var(--main-gray);
  height: 300px;
}

#ahoj p {
  margin: 0px;
  height: 100%;
}

.dropdown-item.active {
  background-color: var(--main-gray);
}

.dropdown-item:focus {
  background-color: var(--main-gray);
}

